import { PanelColor } from "../../../custom-control";

import { PanelBody, SelectControl, ToggleControl, TextControl } from "@wordpress/components";

import { InspectorControls } from "@wordpress/block-editor";

import { __ } from "@wordpress/i18n";

import Typography from "../../../custom-control/typography";

import { getDefaultAttr } from "../../../helpers";

import metadata from "../block.json";

import Dimension from '../../../custom-control/dimension';

const InspectorPanel = (props) => {
  const {
    attributes: {
      color,
      enablePrefix,
      priceType,
      textTypo,
      margin,
      minPrefixText,
      maxPrefixText,
      minMaxPrefixText
    },
    setAttributes,
  } = props;

  const def = getDefaultAttr(metadata);

  return (
      <>
          <InspectorControls>
              <PanelBody title={__("General", "wc-booster")} initialOpen={false}>
                  <SelectControl
                      label={__("Price Types", "wc-booster")}
                      options={[
                          { label: "Minimum", value: "min" },
                          { label: "Maximum", value: "max" },
                          { label: "Minimum To Maximum", value: "min_to_max" },
                          { label: "Maximum To Minimum", value: "max_to_min" },
                      ]}
                      value={priceType}
                      onChange={(priceType) => setAttributes({ priceType })}
                  />
                  <ToggleControl
                      label={__("Enable Prefix", "wc-booster")}
                      checked={enablePrefix}
                      onChange={(enablePrefix) => setAttributes({ enablePrefix })}
                  />
                  {(priceType == 'min' && enablePrefix) && (
                      <TextControl
                          label={__("Prefix Name", "wc-booster")}
                          value={minPrefixText}
                          onChange={(minPrefixText) => setAttributes({ minPrefixText })}
                      />
                  )}
                  {(priceType == 'max' && enablePrefix) && (
                      <TextControl
                          label={__("Prefix Name", "wc-booster")}
                          value={maxPrefixText}
                          onChange={(maxPrefixText) => setAttributes({ maxPrefixText })}
                      />
                  )}
                  {(priceType === 'min_to_max' || priceType === 'max_to_min') && enablePrefix && (
                      <TextControl
                          label={__("Prefix Name", "wc-booster")}
                          value={minMaxPrefixText}
                          onChange={(value) => setAttributes({ minMaxPrefixText: value })}
                      />
                  )}
              </PanelBody>

              <PanelBody title={__("Spacing", "wc-booster")} initialOpen={false}>
                  <Dimension
                      label={__("Margin", "wc-booster")}
                      value={margin}
                      defaultValue={def("margin")}
                      onChange={(margin) => setAttributes({ margin })}
                  />
              </PanelBody>

              <PanelBody title={__('Typography', 'wc-booster')} initialOpen={false}>
                  <Typography
                      label={__('Text', 'wc-booster')}
                      value={textTypo}
                      defaultValue={def("textTypo")}
                      onChange={textTypo => setAttributes({ textTypo })}
                  />
              </PanelBody>
              <PanelBody title={__('Color', 'wc-booster')} initialOpen={false}>
                  <PanelColor
                      colorSettings={[
                          {
                              value: color,
                              onChange: (color) => setAttributes({ color }),
                              label: __('Text Color', 'wc-booster'),
                          }
                      ]}
                  >
                  </PanelColor>
              </PanelBody>
          </InspectorControls>
      </>
  );
};

export default InspectorPanel;
