import { __ } from "@wordpress/i18n";
import { useEffect } from "@wordpress/element";
import { useBlockProps } from "@wordpress/block-editor";
import { prefix } from "../../../constants";
import { withInstanceId, compose } from "@wordpress/compose";
import InspectorPanel from "./inspector";
import { getTypoStyle, getDimensionStyle, isSingleProductTemplate } from "../../../helpers";
import { useSelect } from "@wordpress/data";

const Edit = (props) => {
    const {
        clientId,
        instanceId,
        setAttributes,
        attributes: {
            color,
            enablePrefix,
            priceType,
            textTypo,
            margin,
            minPrefixText,
            maxPrefixText,
            minMaxPrefixText
        },
    } = props;

    const blockProps = useBlockProps();

    useEffect(() => {
        setAttributes({
            block_id: `${prefix}-product-price-${blockProps.id}`,
        });
    }, []);


    let content = null;

    if (priceType === 'min') {
        content = (enablePrefix ? minPrefixText : '') + '$20';
    } else if (priceType === 'max') {
        content = (enablePrefix ? maxPrefixText : '') + '$50';
    } else if (priceType === 'min_to_max') {
        content = (enablePrefix ? minMaxPrefixText : '') + '$20' + ' - ' + '$50';
    } else if (priceType === 'max_to_min') {
        content = (enablePrefix ? minMaxPrefixText : '') + '$50' + ' - ' + '$20';
    }

    const style = {
        ...getDimensionStyle(
            ["marginTop", "marginRight", "marginBottom", "marginLeft"],
            margin,
        ),
        ...getTypoStyle(textTypo),
        color: color,
    };

    return (
        <>
            {isSingleProductTemplate() ? (
                <>
                    <InspectorPanel {...props} />
                    <div {...blockProps}>
                        <div
                            className={`wc-booster-product-price-wrapper wc-booster-pvp`}
                        >
                            <p style={style}>{content}</p>
                        </div>
                    </div>
                </>
            ) : (
                <div {...blockProps}>
                    <div 
                        className="wc-booster-single-product-block-notice">
                        {__('This block is intended for the single product template only.', 'woo-booster')}
                    </div>
                </div>
            )}
        </>
    );

};

export default compose(withInstanceId)(Edit);
